.app {
  height: 100vh;
}

.sections {
  width: 100%;
  height: calc(100vh - 70px);
  position: relative;
  top: 70px;
  scroll-behavior: smooth;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  scrollbar-width: none; // firefox
  &::-webkit-scrollbar {
    display: none;
  }

  > * {
    scroll-snap-align: center;
    width: 100%;
    height: calc(100vh - 70px);
  }
}
